import React, {useEffect, useState} from "react";
import s from '../stylesForClock/alarm.module.css';
import AnalogClock from "./AnalogClock";
import IOSSwitch from "./iosSwitch";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import AlertDialogSlide from "./dialogForm";

const Alarm = () => {
    const [alarmHours, setAlarmHours] = useState(0);
    const [alarmMinutes, setAlarmMinutes] = useState(0);
    const [alarmSeconds, setAlarmSeconds] = useState(0);
    const [alarmActive, setAlarmActive] = useState(false);
    const [isRinging, setIsRinging] = useState(false)
    const [currentTime, setCurrentTime] = useState(new Date());
    const [isOpen, setIsOpen] = useState(false);
    const [alarms, setAlarms] = useState<{ hours: number, minutes: number }[]>([]);

    const openForm = () => {
        setIsOpen(true);
    }
    const handleCloseForm = () => {
        setIsOpen(false);
    };


    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentTime(new Date());
        }, 1000);

        return () => clearInterval(interval);
    }, []);
    const stopAlarm = () => {
        setAlarmActive(false);
        setIsRinging(false);
    };

    useEffect(() => {
        if (alarmActive) {
            const interval = setInterval(() => {
                const now = new Date();
                const currentHours = now.getHours();
                const currentMinutes = now.getMinutes();
                const currentSeconds = now.getSeconds();

                if (
                    currentHours === alarmHours &&
                    currentMinutes === alarmMinutes &&
                    currentSeconds === alarmSeconds
                ) {
                    setIsRinging(true);
                    stopAlarm();
                }
            }, 1000);
            return () => clearInterval(interval);
        }
    }, [alarmActive, alarmHours, alarmMinutes, alarmSeconds]);


    useEffect(() => {
        if (isRinging) {
            alert("Alarm is ringing!");
        }
    }, [isRinging]);


    return (
        <div className={s.alarmBody}>
            <span className={s.alarmTitle}>Alarm</span>
            <AnalogClock date={currentTime}/>

            <div className={s.alarmsList}>
                <div className={s.listElement}>
                    <div className={s.cardContent}>
                        <div className={s.timeCard}>
                            <span>5:39 AM</span>
                            <span>Tomorrow</span>
                        </div>
                        <IOSSwitch onChange={stopAlarm}/>
                    </div>
                </div>

                <div className={s.addAlarmButtonBlock}>
                    <AddCircleIcon className={s.addAlarmButton} onClick={openForm}/>
                </div>
            </div>

            {
                isOpen && (
                    <AlertDialogSlide handleClose={handleCloseForm}/>
                )
            }
        </div>
    );
};

export default Alarm;

